import { Component, h, Host, Prop, Element, State, Watch } from '@stencil/core';
import ValidateInput from '../../input/validate';

@Component({
    tag: 'ks-page-account-data',
    styleUrl: 'data.css'
})
export class PageRegister {
    @Element() root: HTMLElement;
    
    @Prop() heading: string = "Moje dane";
    @Prop() update: string = "Aktualizuj dane";
    @Prop() removeInfo: string = "W celu usunięcia konta skontaktuj się z naszym działem obsługi klienta drogą mailową lub telefoniczną.";

    @Prop() address_id: string;
    @Prop() customer_id: string;
    @Prop() token: string;
    
    @Prop() accountLink: string;
    @Prop() updateApi: string = "";
    @Prop() emailApi: string = "res/true?email=";
    @Prop() emailError: string = "Podany email został już wykorzystany.";
    @Prop() postcodeError: string = "Wprowadź kod pocztowy w formacie XX-XXX";
    @Prop() streetError: string = "Wprowadź conajmniej nazwę ulicy oraz numer domu";
    @Prop() phoneError: string = "Wprowadź prawidłowy numer telefonu";

    @Prop({mutable: true}) country: string = "170";
    @Prop({mutable: true}) company: string = "1";
    @Prop() email: string = "";
    @Prop() companyName: string = "";
    @Prop() companyNip: string = "";
    @Prop() firstname: string = "";
    @Prop() lastname: string = "";
    @Prop() phone: string = "";
    @Prop() city: string = "";
    @Prop() street: string = "";
    @Prop() postcode: string = "";
    
    @State() loading: boolean = false;

    @Watch("country") async countryWatcher() {
        setTimeout(() => {
            (this.root.querySelector("#postcode_input") as HTMLKsInputTextElement).Validate();
        }, 100);
    }

    render() {
        const postcodeRegex = this.country == "170" ? "^[0-9]{2}-[0-9]{3}$" : undefined;

        return <Host>
            <section class="container">
                <h1>{this.heading}</h1>

                <form id="updatedata" action={this.updateApi} method="post" enctype="application/x-www-form-urlencoded" onSubmit={(e)=>this.submit(e)}>
                    <ks-input-dynamic api={this.emailApi} message={this.emailError}>
                        <ks-input-text email required name='email' label="Adres email" icon="mail" value={this.email} />
                    </ks-input-dynamic>

                    <div class="radio">
                        <ks-input-check radio name="osobowosc" value="1" label="Osoba fizyczna" checked={this.company == "1"} onChange={() => this.company = "1"} />
                        <ks-input-check radio name="osobowosc" value="0" label="Firma"          checked={this.company == "0"} onChange={() => this.company = "0"}/>
                    </div>

                    {this.company == "0" && <div class="columns">
                        <ks-input-text required name='nazwa_firmy' label="Nazwa Firmy" icon="briefcase" value={this.companyName} />
                        <ks-input-text required name='nip_firmy' label="Numer NIP" icon="briefcase" value={this.companyNip}  />
                    </div>}

                    <ks-input-select name='panstwo' label="Kraj" onChange={e => this.country = (e.target as HTMLSelectElement).value}>
                        <slot name="country" />
                    </ks-input-select>

                    <div class="columns">
                        <div>
                            <ks-input-text required name='imie' label="Imię" icon="user" value={this.firstname} />
                            <ks-input-text required name='nazwisko' label="Nazwisko" icon="user" value={this.lastname} />
                            <ks-input-text required phone name='telefon' label="Numer Telefonu" icon="phone" value={this.phone} />
                        </div>

                        <div>
                            <ks-input-text required name='miasto' label="Miasto" icon="map-pin" value={this.city} />
                            <ks-input-text required name='ulica' label="Ulica i numer domu / lokalu" icon="map-pin" value={this.street} regex="[a-zа-я].*[0-9]|[0-9].*[a-zа-я]" regexMessage={this.streetError}/>
                            <ks-input-text required id="postcode_input" name='kod_pocztowy' label="Kod Pocztowy" icon="map-pin" value={this.postcode} regex={postcodeRegex} regexMessage={this.postcodeError} />
                        </div>
                    </div>

                    <input type="hidden" value="1" name="przetwarzanie" />
                    <input type="hidden" value="1" name="przetwarzanie_dodatkowe" />
                    <input type="hidden" name="adres_id" value={this.address_id} />
                    <input type="hidden" name="id_klienta" id="id_klienta" value={this.customer_id} />
                    <input type="hidden" name="tok" value={this.token} />
                    <input type="hidden" name="akcja" value="zapisz" />


                    <p>{this.removeInfo}</p>

                    <div class="buttons">
                        <ks-button rounded submit name={this.update} loading={this.loading} />
                        <ks-button rounded border link={this.accountLink} name="Wróć do panelu klienta" loading={this.loading} />
                    </div>
                </form>
            </section>
        </Host>;
    }

    async submit(event: Event) {
        this.loading = true;
        
        const form = this.root.querySelector("#updatedata") as HTMLFormElement;
        
        if(!await ValidateInput(form)) {
            this.loading = false;
            event.preventDefault();
            return;
        }
    }
}
